.home_skills {
    min-height: 368px;
    background-color: $trang;
    display: flex;
    justify-content: center;
    text-align: center;
    p {
        margin-top: 1.5rem;
        text-align: center; }

    h2 {
        position: relative;
        font-size: 2rem;
        margin-top: 40px;
        text-align: center;
        margin-bottom: 1.5rem;
        color: $chu;
        &:before {
                content: "";
                position: absolute;
                width: 10%;
                height: 3px;
                bottom: 0px;
                left: 39%;
                top: 50px;
                transform: translateX(50%);
                opacity: 0.4;

                background: grey; } }
    .progress-circle {
        position: relative;
        display: inline-block;
        margin: 1rem;
        width: 140px;
        height: 140px;
        border-radius: 50%;
        background-color: $color-progress-circle;
        &:after {
            content: '';
            display: inline-block;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            -webkit-animation: colorload 2s;
            animation: colorload 2s; }
        span {
            font-size: 1.5rem;
            color: $color-progress-number;
            position: absolute;
            left: 50%;
            top: 50%;
            display: block;
            width: 120px;
            height: 120px;
            line-height: 120px;
            margin-left: -60px;
            margin-top: -60px;
            text-align: center;
            border-radius: 50%;
            background: $color-progress-circle-number;
            z-index: 1; } }

    .progress-90 {
        &:after {
            background-image: linear-gradient(54deg, $color-progress-idicator 50%, transparent 50%, transparent), linear-gradient(270deg, $color-progress-idicator 50%, $color-progress-circle 50%, $color-progress-circle); } }

    .progress-75 {
        &:after {
              background-image: linear-gradient(0deg, #d74680 50%, transparent 50%, transparent), linear-gradient(270deg, #d74680 50%, $color-progress-circle 50%, $color-progress-circle); } }

    .progress-70 {
        &:after {
              background-image: linear-gradient(-18deg, #17c2a4 50%, transparent 50%, transparent), linear-gradient(270deg, #17c2a4 50%, $color-progress-circle 50%, $color-progress-circle); } }

    .progress-85 {
        &:after {
             background-image: linear-gradient(36deg, #eb7d4b 50%, transparent 50%, transparent), linear-gradient(270deg, #eb7d4b 50%, $color-progress-circle 50%, $color-progress-circle); } }

    @-webkit-keyframes colorload {
        0% {
            opacity: 0;
            transform: rotate(0deg); }
        100% {
            opacity: 1;
            transform: rotate(360deg); } } }

